<template>
    <b-tabs 
        :expanded="items.expanded"
        :type="items.tabSubtype"
        :size="items.tabSize"
        :position="items.tabPosition"
        v-model="selection"
        @input="input">
        <b-tab-item 
            v-for="(item, index) in items.data" 
            :key="index"
            :icon="item.icon"
            :visible="items.visible"
            :label="item.title" 
            :disabled="item.disabled">
            <template v-if="item.tabHeader" slot="header">
                <b-icon :icon="item.icon"></b-icon>
                <span> {{ item.title}} <b-tag v-if="item.count" rounded> {{ item.count }} </b-tag> </span>
            </template>
            {{ item.content}}
        </b-tab-item>
    </b-tabs>
</template>

<script>
    export default {
        props: ['opts', 'field'],
        data() {
            return {
                items: {},
                selection: null
            }
        },
        methods: {
            input() {
                this.$emit('selection', 'value', this.selection)
            }
        },
        beforeMount: function () {
            if (this.opts != null) {
                this.items = this.opts;
            }
        }
    }
</script>