<template>
    <div :class="items.fontAlignment">
        <h1 v-if="items.title" class="title" :class="title" :id="items.anchor">
            <a v-if="items.anchor" :href="'#' + items.anchor" aria-hidden="true" class="header-anchor">#</a>
            {{ items.title }}
        </h1>
        <h2 v-if="items.subtitle" class="subtitle" :class="subtitle" :id="items.anchor">
            <a v-if="items.anchor" :href="'#' + items.anchor" aria-hidden="true" class="header-anchor">#</a>
            {{ items.subtitle }}
        </h2>
        <h3 v-if="items.heading3" class="title is-3" :class="{'is-spaced': items.spaced}">{{ items.heading3 }}</h3>
        <h4 v-if="items.heading4" class="title is-4" :class="{'is-spaced': items.spaced}">{{ items.heading4 }}</h4>
        <h5 v-if="items.heading5" class="title is-5" :class="{'is-spaced': items.spaced}">{{ items.heading5 }}</h5>
        <h6 v-if="items.heading6" class="title is-6" :class="{'is-spaced': items.spaced}">{{ items.heading6 }}</h6>
    </div>
</template>

<script>
    export default {
        props: ['opts'],
        data() {
            return {
                items: {}
            }
        },
        watch: {
            'opts': {
                handler() {
                    if (this.opts != null) {
                        this.items = Object.assign({}, this.opts);
                    }
                },
                deep: true
            }
        },
        computed: {
            title() {
                return [
                    {'is-underlined': this.items.underlined || (typeof this.settings != 'undefined' ? this.settings.underlined : false)},
                    {'is-spaced': this.items.spaced || (typeof this.settings != 'undefined' ? this.settings.spaced : false)},
                    this.items.titleSize,
                    this.items.titleColour
                ]
            },
            subtitle() {
                return [
                    {'is-underlined': this.items.underlined || (typeof this.settings != 'undefined' ? this.settings.underlined : false)},
                    {'is-spaced': this.items.spaced || (typeof this.settings != 'undefined' ? this.settings.spaced : false)},
                    this.items.subtitleSize,
                    this.items.subtitleColour
                ]
            }
        },
        beforeMount: function () {
            if (this.opts != null) {
                this.items = this.opts;
            }
        }
    }
</script>

<style scoped>
a.header-anchor {
    font-size: .85em;
    float: left;
    margin-left: -.87em;
    padding-right: .23em;
    margin-top: .125em;
    opacity: 0;
}
h1:hover .header-anchor, h2:hover .header-anchor, h3:hover .header-anchor, h4:hover .header-anchor, h5:hover .header-anchor, h6:hover .header-anchor {
    opacity: 1;
}
.is-underlined {
    padding-bottom: .3rem;
    border-bottom: 1px solid #eaecef;
}
</style>

