<template>
    <section class="hero" :class="hero">
        <div v-if="items.navbar" class="hero-head">
            <s-navbar :settings="settings"></s-navbar>
        </div>
        <div class="hero-body" :class="heroBody">
            <s-heading :opts="items"></s-heading>
        </div>
        <div v-if="items.heroFooterBlocks" class="hero-foot">
            <s-component
                v-for="(item, index) in items.blocks"
                :key="index"
                :is="item.type"
                :opts="item">
            </s-component>
        </div>
    </section>
</template>

<script>
    export default {
        props: ['opts', 'settings'],
        data() {
            return {
                items: {}
            }
        },
        watch: {
            'opts': {
                handler() {
                    if (this.opts != null) {
                        this.items = Object.assign({}, this.opts);
                    }
                },
                deep: true
            }
        },
        computed: {
            hero() {
                return [
                    {'is-bold': this.items.bold || (typeof this.settings != 'undefined' ? this.settings.bold : false) },
                    this.items.colour || (typeof this.settings != 'undefined' ? this.settings.colour : ''),
                    this.items.size || (typeof this.settings != 'undefined' ? this.settings.size : '')
                ];
            },
            heroBody() {
                return [
                    {'is-paddingless': this.items.heroPaddingless || (typeof this.settings != 'undefined' ? this.settings.heroPaddingless : '')}
                ];
            }
        },
        beforeMount: function () {
            if (this.opts != null) {
                this.items = this.opts;
            }
        }
    }
</script>
