<template>
    <nav class="level" :class="level">
        <div v-if="items.right" class="level-left">
            <div class="level-item" :class="item.fontAlignment" v-for="(item, index) in items.blocks" v-bind:key="index">
                <s-component
                    :is="item.type"
                    :opts="item">
                </s-component>
            </div>
        </div>
        <div v-if="items.right" class="level-right">
            <div class="level-item" :class="item.fontAlignment" v-for="(item, index) in items.secondaryBlocks" v-bind:key="index">
                <s-component
                    :is="item.type"
                    :opts="item">
                </s-component>
            </div>
        </div>
        <div v-if="!items.right" class="level-item" :class="items.fontAlignment">
            <div v-for="(item, index) in items.blocks" v-bind:key="index">
                <s-component
                    :is="item.type"
                    :opts="item">
                </s-component>
            </div>
        </div>
    </nav>
</template>

<script>
    export default {
        props: ['opts'],
        data() {
            return {
                items: {}
            }
        },
        computed: {
            level() {
                return [
                    {'is-mobile': this.items.mobile},
                    {'is-desktop': this.items.desktop}
                ]
            }
        },
        beforeMount: function () {
            if (this.opts != null) {
                this.items = this.opts;
            }
        }
    }
</script>