<template>
    <div v-if="items.blocks && items.grouped" class="field" :class="fieldClasses">
        <p class="control" v-for="(item, index) in items.blocks" v-bind:key="index">
            <a class="button" :class="[item.colour, item.size, {'is-fullwidth': item.expanded}, {'is-static': item.static}, {'is-outlined': item.outlined}, {'is-inverted': item.inverted}, {'is-rounded': item.rounded}, {'is-hovered': item.hoverable}, {'is-focused': item.focusable}, {'is-active': item.active}, {'is-loading': item.loading}, {'is-selected': item.selected}]">
                <span>{{ item.title }}</span>
            </a>
        </p>
    </div>
    <div v-else-if="items.blocks" class="buttons" :class="buttonsClasses">
        <span class="button" 
            :class="[item.colour, item.size, {'is-fullwidth': item.expanded}, {'is-static': item.static}, {'is-outlined': item.outlined}, {'is-inverted': item.inverted}, {'is-rounded': item.rounded}, {'is-hovered': item.hoverable}, {'is-focused': item.focusable}, {'is-active': item.active}, {'is-loading': item.loading}, {'is-selected': item.selected}]" 
            v-for="(item, index) in items.blocks" 
            v-bind:key="index">
            {{ item.title }}
        </span>
    </div>
    <div v-else>
        <a class="button" :class="buttonClasses" :disabled="items.disabled">
            <b-icon v-if="items.icon && !items.iconRight" :icon="items.icon"></b-icon>
            <span>{{ items.title }}</span>
            <b-icon v-if="items.icon && items.iconRight" :icon="items.icon"></b-icon>
        </a>
    </div>
</template>

<script>
    export default {
        props: ['opts', 'settings'],
        data() {
            return {
                items: {}
            }
        },
        computed: {
            fieldClasses() {
                return [
                    {'is-grouped': this.items.grouped},
                    {'has-addons': this.items.addons},
                    this.items.size

                ]
            }, 
            buttonsClasses() {
                return [
                    {'is-right': this.items.right},
                    {'has-addons': this.items.addons},
                    this.items.size

                ]
            },   
            buttonClasses() {
                return [
                    this.items.colour || (typeof this.settings != 'undefined' ? this.settings.colour : ''),
                    this.items.size || (typeof this.settings != 'undefined' ? this.settings.size : ''),
                    {'is-fullwidth': this.items.expanded},
                    {'is-static': this.items.static},
                    {'is-outlined': this.items.outlined},
                    {'is-inverted': this.items.inverted},
                    {'is-rounded': this.items.rounded},
                    {'is-hovered': this.items.hoverable},
                    {'is-focused': this.items.focusable},
                    {'is-active': this.items.active},
                    {'is-loading': this.items.loading}
                ];
            }
        },
        beforeMount: function () {
            if (this.opts != null) {
                this.items = this.opts;
            }
        }
    }
</script>