<template>
    <section class="hero">
    <div class="hero-body">
        <div class="container has-text-centered">
            <h1 class="title is-family-code">
                Buetify
            </h1>
            <h2 class="subtitle">
                The dynamic website generator.
            </h2>
            <div>
                <s-component :data="items" :settings="settings"></s-component>
            </div>
        </div>
    </div>
    </section>
</template>

<script>
    const demoBlocks = require('./data/component_demo.json');
    export default {
        props: ['opts', 'settings'],
        data() {
            return {
                blocks: [],
                items: [],
                itemsLength: 0,
                demoBlocks,
                count: 0
            }
        },
        methods: {
            toggle() {
                this.blocks = demoBlocks
                this.itemsLength = this.blocks.length;
            }
        },
        beforeMount() {
            this.toggle();
            setInterval(() => {
                this.items = [];
                this.items.push(this.blocks[this.count % this.itemsLength]);
                this.items.push();
                this.count += 1;
            }, 1500);

        }
    }
</script>
